.card:first-of-type::before {
  content: "";
  position: absolute;
  display: block;
  top: 45%;
  left: -18%;
  width: 180px;
  height: 180px;
  border: 27px solid #0ad9c6;
  border-radius: 50%;
  transform: translateY(-50%);
  z-index: -1;
}
@media (max-width: 1439px) {
  .card:first-of-type::before {
    width: 127px;
    height: 127px;
    border-width: 18px;
    top: unset;
    bottom: -10%;
    left: -13%;
  }
}
@media (max-width: 767px) {
  .card:first-of-type::before {
    bottom: -11%;
    left: -9%;
  }
}
.card:nth-of-type(2n)::before {
  content: "";
  position: absolute;
  display: block;
  top: 3%;
  left: 1%;
  width: 47px;
  height: 47px;
  border: 8px solid #0ad9c6;
  transform: translateX(-50%);
  z-index: 5;
}
@media (max-width: 1439px) {
  .card:nth-of-type(2n)::before {
    display: none;
  }
}
.card:nth-of-type(2n)::after {
  content: "";
  position: absolute;
  display: block;
  bottom: 1%;
  right: 0;
  width: 47px;
  height: 47px;
  border: 8px solid #0ad9c6;
  transform: translate(50%, -50%);
  z-index: 5;
}
@media (max-width: 1439px) {
  .card:nth-of-type(2n)::after {
    display: none;
  }
}
.card:nth-of-type(3n)::before {
  content: "";
  position: absolute;
  display: block;
  top: 15px;
  right: 10px;
  width: 95px;
  height: 95px;
  border: 17px solid #0ad9c6;
  border-radius: 50%;
  transform: translate(50%, -50%);
  z-index: 5;
}
@media (max-width: 1439px) {
  .card:nth-of-type(3n)::before {
    width: 68px;
    height: 68px;
    border-width: 13px;
  }
}
@media (max-width: 767px) {
  .card:nth-of-type(3n)::before {
    top: 15px;
    right: 20px;
  }
