.promo-video {
  background-color: #000000;
}

.promo-video__wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.promo-video img {
  width: 100%;
  height: 100%;
  border: none;
  object-fit: cover;
}

.promo-video button {
  position: absolute;
  top: 50%;
  left: 50%;
  padding: 0;
  transform: translate(-50%, -50%);
  border: none;
  border-radius: 50%;
  background-color: #0ad9c6;
  background-size: cover;
  width: 65px;
  height: 65px;
  z-index: 2;
  cursor: pointer;
}
.promo-video button:hover {
  opacity: 0.8;
  background-color: #ffffff;
}
.promo-video button:focus {
  opacity: 0.8;
  background-color: #ffffff;
}
.promo-video button:active {
  opacity: 1;
  background-color: #ffffff;
}
.promo-video button:disabled {
  opacity: 0.3;
  background-color: #0ad9c6;
  pointer-events: none;
}
.promo-video button::before {
  content: "";
  display: block;
  position: absolute;
  margin-left: 8px;
  top: 50%;
  left: 50%;
  border: 10px solid transparent;
  border-left: 15px solid #000000;
  transform: translate(-50%, -50%);
}

.video iframe {
  width: 100%;
  height: 100%;
  border: none;
}

